import { extendTheme } from "@chakra-ui/react";

// Definiowanie niestandardowych breakpointów
const breakpoints = {
  base: "344px",         // Surface.. Dla ekranów mniejszych niż 375px
  sm: "375px",         // iPhone SE lub inne mniejsze ekrany
  md: "412px",       
  galaxy: "414px",  // iPhone XR, Galaxy S20 Ultra
  iphone12Pro: "390px", // iPhone 12 Pro
  iphone14Pro: "430px",
  samsungg8: "360px",
  surface: "540px",
  ipadm: "768px",
  ipada: "820px",
  lg: "853px",  // asus
  surfacepro: "912px",         // iPad m  standardowy breakpoint dla tabletów
  xl: "1024px",        // ipad p  nest hub
 hubm: "1280px",     // Na bardzo dużych ekranach desktopowych nest hub max
 computer: "1920px",

};

const theme = extendTheme({
  breakpoints,
  fonts: {
    heading: "'Poppins', sans-serif",  // Czcionka dla nagłówków
    body: "'Lato', sans-serif",        // Czcionka dla tekstu głównego
  },
});

export default theme;

