import React from "react";
import { Heading, VStack, Box, Text, Image, useBreakpointValue } from "@chakra-ui/react";
import imageLeft from '../images/IMG_3280.png'; 

const greeting = "Hi there !";
const bio1 = "I'm Ania";
const bio2 = "I'm a passionate aspiring front-end developer with a love for creating beautiful, intuitive websites. Specializing in React.js, I strive to bring ideas to life through clean, responsive design. In my projects, I blend technical skills with an artistic spirit, always guided by a strong sense of aesthetics and precision.";

const LandingSection = () => {
  // Dynamiczna szerokość obrazu
  const imageWidth = useBreakpointValue({
    base: '100%',       // Na najmniejszych ekranach
    sm: '120%',         // iPhone SE lub inne mniejsze ekrany
    md: '100%',
    galaxy: '100%',          // iPhone XR, Galaxy S20 Ultra
    iphone12Pro: '100%', // iPhone 12 Pro
    iphone14Pro: '100%',
    ipadm: '80%',
    ipada: '85%',
    surface: '85%',
    lg: '70%',
    surfacepro: '90%',          
    xl: '70%',          // Na większych ekranach desktopów
    "2xl": '40%',       // Na dużych ekranach
  });

  // Dynamiczna wysokość obrazu
  const imageHeight = useBreakpointValue({
    base: '50vh',      // Na najmniejszych ekranach
    sm: '70vh', 
    iphone12Pro: '90vh', // iPhone 12 Pro
    iphone14Pro: '90vh',
    pixel7: '90vh',      
    md: '90vh',
    samsungg8: '90vh',
    galaxy: '90vh',
    surface: '90vh',
    ipadm: '90vh',
    ipada: '90vh',
    lg: '90vh',
    surfacepro: '90vh',
    xl: '90vh',       // Na większych ekranach desktopów
  });

  // Dynamiczne przesunięcie zdjęcia
  const imageMarginTop = useBreakpointValue({
    base: '36%',        // Na najmniejszych ekranach
    sm: '-22%',
    iphone12Pro: '28%', // iPhone 12 Pro
    iphone14Pro: '44%',     
    md: '39%',        // Pixel 7
    samsungg8: '-7%',
    surface: '-3%',
    galaxy: '34%',
    ipadm: '27%',
    ipada: '39%',
    lg: '46%', 
    surfacepro: '44%',        // iPad Pro
    xl: '-26%',          // Na większych ekranach desktopów
  });

  const imageMarginLeft = useBreakpointValue({
    base: '0',       // Na mniejszych ekranach brak przesunięcia
    xl: '-97px',    // Na szerokich ekranach przesunięcie obrazu o 100px w lewo
    ipadm: '35px',
  });

  const imageMarginBottom = useBreakpointValue({
    base: '0%',        // Na najmniejszych ekranach
    sm: '0%',
    iphone12Pro: '0%', // iPhone 12 Pro
    iphone14Pro: '0%',
    md: '0%',
    galaxy: '0%',
    samsungg8: '0%',
    surface: '0%',
    ipadm: '0%',
    ipada: '0%',
    lg: '0%',  // asus
    surfacepro: '0%',         // iPad m  standardowy breakpoint dla tabletów
    xl: '0%',        // ipad p  nest hub
   hubm: '0%',     // Na bardzo dużych ekranach desktopowych nest hub max
   computer: '0%',
  });

  return (
    <Box
      height="100vh"
      width="100vw"
      backgroundColor="black"
      padding="0"
      margin="0"
      display="flex"
      flexDirection="column"
      boxSizing="border-box"
    >
      {/* Kontener dla tekstu */}
      <VStack
        spacing={6}
        alignItems="center"
        w="90%"
        p={4}
      >
        {/* Nagłówek z tekstem "Hi there!" */}
        <Heading as="h2" fontSize="4xl" fontWeight="100" fontFamily="'Oooh Baby', cursive" color="white">
          {greeting}
        </Heading>
        {/* Nagłówek h1 */}
        <Heading as="h1" fontSize="3xl" fontWeight="bold" fontFamily="'Old Standard TT'" color="white">
          {bio1}
        </Heading>
        {/* Tekst */}
        <Text fontSize="lg" fontWeight="normal" fontFamily="'Montserrat', sans-serif" color="white">
          {bio2}
        </Text>
      </VStack>

      {/* Zdjęcie wyświetlane dynamicznie */}
      <Image
        src={imageLeft}
        width={imageWidth}      // Dynamiczna szerokość obrazu
        height={imageHeight}    // Dynamiczna wysokość obrazu
        marginTop={imageMarginTop} // Dynamiczne przesunięcie w górę
        marginLeft={imageMarginLeft} // Tutaj dodaj przesunięcie w lewo
        objectFit="contain"     // Zachowanie proporcji zdjęcia
      />
    </Box>
  );
};

export default LandingSection;
